@import url(https://use.typekit.net/ojv4ekx.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.results {
  width: 100%; }

.results-container {
  width: 80%;
  margin: 0 auto; }

.results-row {
  background-color: white; }

.App {
  overflow: hidden;
  text-align: center;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(59, 59, 59, 0.68)), to(rgba(0, 0, 0, 0.68))), url(/Meteorite-Explorer/static/media/wallpaper.c4294994.jpg);
  background-image: -webkit-linear-gradient(rgba(59, 59, 59, 0.68), rgba(0, 0, 0, 0.68)), url(/Meteorite-Explorer/static/media/wallpaper.c4294994.jpg);
  background-image: linear-gradient(rgba(59, 59, 59, 0.68), rgba(0, 0, 0, 0.68)), url(/Meteorite-Explorer/static/media/wallpaper.c4294994.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh; }

* {
  margin: 0;
  padding: 0;
  font-family: 'eurostile'; }

.app-header {
  width: 100%;
  font-size: 2em; }
  .app-header h2 {
    margin-top: 5em;
    text-transform: uppercase;
    color: white; }

.form .inputs {
  margin: 2em auto; }

.form input[type=text] {
  width: 25em;
  margin: 0 2em;
  height: 2em;
  padding: 0 0.5em; }

.form input[type=submit] {
  width: 6em;
  background-color: white;
  height: 2em;
  border-radius: 3px;
  border: 1px solid white; }
  .form input[type=submit]:hover {
    cursor: pointer;
    background-color: blue;
    border: 1px solid blue;
    color: white; }

